[class^="icon-"]:before,
[class*=" icon-"]:before
  font-family: Fontello
  font-style: normal
  font-weight: normal
  speak: never
  display: block
  margin: 0
  text-decoration: inherit
  text-align: center
  font-variant: normal
  text-transform: none

/* your fontello items here */
.icon-arrow_down:before
  content: '\e800'
.icon-check:before
  content: '\e801'
.icon-close:before
  content: '\e802'
.icon-link_arrow2:before
  content: '\e803'
.icon-play:before
  content: '\e804'
.icon-slide_arrow:before
  content: '\e806'
.icon-link_arrow:before
  content: '\e807'
.icon-vkontakte:before
  content: '\f189'
.icon-paper-plane:before
  content: '\f1d8'
.icon-whatsapp:before
  content: '\f232'