@import "settings"
@import "_mixins/_mixins"
@import "_fonts/fonts_list"
@import "_fonts/fontello"                                                                                               // if you use fontello fonts
@import "_components/reset"
@import "_components/header"

/* main page */
.main-page
  background-color: $main-red-color
  border-radius: 0 0 0 175px
  width: 100%
  max-width: 1404px
  margin: 0 auto
  &__title
    color: #fff
    font-family: DINCondensed, sans-serif
    font-weight: 400
    font-size: 42px
    line-height: 110%
    letter-spacing: -.02em
    @media (max-width: 419px)
      line-height: 42px
  &__descr
    max-width: 375px
    margin-top: 38px
    color: #fff
    font-size: 18px
    line-height: 139%
    @media (max-width: 419px)
      font-size: 16px
      line-height: 20px
      margin-top: 12px
  &__btn
    display: inline-block
    background-color: $yellow-color
    text-transform: uppercase
    color: #000
    font-family: DINCondensed, sans-serif
    font-weight: 400
    font-size: 18px
    line-height: 20px
    letter-spacing: .03em
    padding: 20px 31px 17px
    border-radius: 30px
    margin-top: 33px
    transition: background-color .3s
    @media (max-width: 419px)
      margin-top: 25px
      font-size: 16px
      line-height: 25px
      padding: 9px 32px 7px
    &:hover
      background-color: $yellow-color-hover
  &__content
    display: flex
    align-items: stretch
    justify-content: flex-start
    flex-wrap: wrap
    &__left-side
      flex: 0 0 50%
      max-width: 50%
      padding: 55px 15px 55px 0
      min-height: 450px
      @media (max-width: 1199px)
        flex: 0 0 45%
        max-width: 45%
        min-height: 400px
      @media (max-width: 959px)
        flex: 0 0 55%
        max-width: 55%
        min-height: 320px
        padding: 35px 15px 35px 0
      @media (max-width: 639px)
        flex: 0 0 100%
        max-width: 100%
        padding: 20px 0
      @media (max-width: 419px)
        padding: 0
        min-height: auto
    &__right-side
      flex: 0 0 50%
      max-width: 50%
      position: relative
      @media (max-width: 1199px)
        flex: 0 0 55%
        max-width: 55%
      @media (max-width: 959px)
        flex: 0 0 45%
        max-width: 45%
      @media (max-width: 639px)
        flex: 0 0 100%
        max-width: 100%
        margin-bottom: -50px
      @media (max-width: 419px)
        margin-top: 45px
        margin-bottom: -130px
      .bg-wrapper
        position: absolute
        top: 15px
        left: 0
        width: calc(120% + 36px)
        height: calc(100% + 28px)
        background-repeat: no-repeat
        background-position: top left
        background-size: cover
        @media (max-width: 1404px)
          width: calc(100% + ((100vw - 1110px) / 2))
        @media (max-width: 1199px)
          width: calc(100% + ((100vw - 960px) / 2) + 15px)
        @media (max-width: 959px)
          width: calc(100% + 15px)
        @media (max-width: 767px)
          height: 365px
          top: auto
          bottom: -30px
        @media (max-width: 639px)
          bottom: auto
          top: 0
          position: relative
        @media (max-width: 419px)
          height: 280px
          bottom: 0
      .circle
        display: block
        position: absolute
        z-index: 1
        left: -90px
        bottom: 28px
        @media (max-width: 959px)
          left: -70px
          bottom: 0
        @media (max-width: 767px)
          bottom: -20px
          left: -65px
        @media (max-width: 639px)
          left: auto
          right: 0
          bottom: -90px
        @media (max-width: 419px)
          right: 0px
          bottom: -85px
        svg
          @media (max-width: 959px)
            transform: scale(.8)
          @media (max-width: 767px)
            transform: scale(.7)
          @media (max-width: 639px)
            transform: scale(.9)
            rect
              stroke: #F3F3F3
          @media (max-width: 419px)
            transform: scale(.75)
        .circle-text
          transform-origin: center
          animation: spin 10s linear infinite
.main-descr
  padding: 140px 0 40px
  @media (max-width: 1199px)
    padding: 100px 0 40px
  @media (max-width: 959px)
    padding: 80px 0 40px
  @media (max-width: 639px)
    padding: 140px 0 40px
  @media (max-width: 419px)
    padding: 215px 0 40px
  &__title
    font-weight: 400
    font-size: 24px
    line-height: 125%
    letter-spacing: -.02em
    @media (max-width: 575px)
      font-size: 22px
    @media (max-width: 419px)
      font-size: 18px
      line-height: 24px
  &__content
    display: flex
    align-items: flex-start
    justify-content: flex-start
    @media (max-width: 959px)
      flex-wrap: wrap
    &__left-side
      display: flex
      flex-wrap: wrap
      align-items: flex-start
      justify-content: flex-start
      flex: 0 0 50%
      max-width: 50%
      @media (max-width: 1199px)
        flex: 0 0 45%
        max-width: 45%
      @media (max-width: 959px)
        flex: 0 0 100%
        max-width: 100%
      .descr-item
        flex: 0 0 50%
        max-width: 200px
        margin: 0 auto 40px 0
        @media (max-width: 959px)
          flex: 0 1 auto
          max-width: unset
          margin: 0 auto 30px 0
        @media (max-width: 639px)
          flex: 0 0 50%
          max-width: 50%
        @media (max-width: 419px)
          margin: 0 0 35px 0
        &:nth-of-type(2n)
          margin-right: auto
          @media (max-width: 419px)
            padding-left: 20px
        &:nth-of-type(1)
          @media (max-width: 959px)
            margin-left: 0
        &:last-child
          @media (max-width: 959px)
            margin-right: 0
        .number-block
          font-family: DINCondensed, sans-serif
          font-weight: 700
          font-size: 120px
          line-height: 100%
          background: linear-gradient(74.43deg, #DA0C39 7.11%, #FDC62C 94.7%)
          -webkit-background-clip: text
          -webkit-text-fill-color: transparent
          background-clip: text
          text-fill-color: transparent
          @media (max-width: 1199px)
            font-size: 90px
          @media (max-width: 959px)
            font-size: 75px
          @media (max-width: 767px)
            font-size: 70px
          @media (max-width: 639px)
            font-size: 82px
        .descr-block
          font-family: DINCondensed, sans-serif
          font-weight: 400
          font-size: 22px
          line-height: 24px
          text-transform: uppercase
          max-width: 150px
          @media (max-width: 959px)
            max-width: 135px
          @media (max-width: 767px)
            font-size: 20px
            line-height: 22px
          @media (max-width: 639px)
            font-size: 22px
            line-height: 24px
            max-width: 100%
          @media (max-width: 419px)
            font-size: 18px
            line-height: 18px
            padding-right: 10px
    &__right-side
      flex: 0 0 50%
      max-width: 50%
      @media (max-width: 1199px)
        flex: 0 0 55%
        max-width: 55%
      @media (max-width: 959px)
        flex: 0 0 100%
        max-width: 100%
        margin-top: 10px
      @media (max-width: 419px)
        margin-top: 0
      .advantage-list
        display: flex
        flex-wrap: wrap
        align-items: flex-start
        justify-content: flex-start
        margin-top: 25px
        @media (max-width: 959px)
          margin-top: 35px
        @media (max-width: 419px)
          margin-top: 25px
        &__item
          flex: 0 0 auto
          max-width: 33.3%
          margin: 0 auto 0 0
          @media (max-width: 639px)
            flex: 0 0 100%
            max-width: 100%
            margin: 20px 0 0
          @media (max-width: 419px)
            margin: 30px 0 0
          &:nth-of-type(1)
            padding-right: 10px
            @media (max-width: 959px)
              padding-right: 15px
            @media (max-width: 639px)
              padding-right: 0
              margin-top: 0
          &:nth-of-type(3)
            padding-left: 10px
            @media (max-width: 959px)
              padding-left: 15px
            @media (max-width: 639px)
              padding-left: 0
          .item-title
            text-transform: uppercase
            font-family: DINCondensed, sans-serif
            font-weight: 400
            font-size: 24px
            line-height: 104%
            letter-spacing: .03em
            color: $red-title-color
          .item-descr
            margin-top: 18px
            font-size: 16px
            line-height: 125%
            @media (max-width: 639px)
              margin-top: 10px
      .partner-block
        display: flex
        align-items: center
        justify-content: flex-start
        width: 100%
        margin-top: 34px
        background-color: $main-red-color
        border-radius: 15px
        position: relative
        @media (max-width: 969px)
          max-width: calc(100% - 15px)
          margin: 35px 0 0 auto
        @media (max-width: 419px)
          margin: 50px 0 0 auto
          max-width: 100%
        .partner-image
          display: block
          width: auto
          max-width: 100%
          margin: 0
          position: absolute
          top: calc(50% - 35px)
          left: -20px
          @media (max-width: 575px)
            max-width: 128px
            top: calc(50% - 32px)
          @media (max-width: 419px)
            left: -5px
        .descr-block
          flex: 0 0 calc(100% - 165px)
          max-width: calc(100% - 165px)
          margin: 0 0 0 auto
          color: #fff
          font-size: 18px
          line-height: 25px
          padding: 25px 60px 20px 0
          @media (max-width: 575px)
            flex: 0 0 calc(100% - 130px)
            max-width: calc(100% - 130px)
            padding: 20px 20px 20px 0
          @media (max-width: 419px)
            flex: 0 0 calc(100% - 140px)
            max-width: calc(100% - 140px)
            line-height: 20px

/* category page */
.category-page
  .category-image
    position: absolute
    width: auto
    max-width: calc(100% + 10px)
    left: 15px
    top: 25px
    @media (max-width: 1199px)
      top: 0
    @media (max-width: 1023px)
      left: -20px
      max-width: 100%
    @media (max-width: 959px)
      top: -30px
    @media (max-width: 767px)
      top: 0
    @media (max-width: 639px)
      position: relative
      top: 0
      left: 0
      max-width: 400px
      margin: 0 auto
    @media (max-width: 575px)
      max-width: 100%
.category-list
  margin-top: 88px
  @media (max-width: 1199px)
    margin-top: 65px
  @media (max-width: 959px)
    margin-top: 35px
  .category-link
    display: inline-flex
    align-items: center
    justify-content: flex-start
    font-size: 24px
    line-height: 24px
    padding: 10px 35px
    @media (max-width: 639px)
      font-size: 20px
      line-height: 20px
      padding: 10px 25px
    @media (max-width: 420px)
      width: 100%
      justify-content: center
      padding: 12px 25px
    .icon
      transform: scale(-1,1)
      margin-right: 30px
      font-size: 16px
      line-height: 16px
  &__content
    display: flex
    align-items: stretch
    justify-content: flex-start
    flex-wrap: wrap
    margin-top: 70px
    @media (max-width: 1199px)
      margin-top: 45px
    @media (max-width: 959px)
      margin-top: 30px
    &__item
      flex: 0 0 calc(33.3% - 10px)
      max-width: calc(33.3% - 10px)
      margin: 0 15px 30px 0
      position: relative
      border-radius: 24px
      overflow: hidden
      display: flex
      flex-direction: column
      align-items: flex-start
      justify-content: flex-start
      @media (max-width: 768px)
        flex: 0 0 calc(50% - 10px)
        max-width: calc(50% - 10px)
        margin: 0 20px 20px 0
        &:nth-of-type(2n)
          margin-right: 0
      @media (max-width: 575px)
        flex: 0 0 100%
        max-width: 100%
        margin: 0 0 20px 0
      &:nth-of-type(3n)
        margin-right: 0
        @media (max-width: 959px)
          margin-right: 20px
        @media (max-width: 575px)
          margin-right: 0
      .item-link
        position: absolute
        top: 0
        left: 0
        display: block
        width: 100%
        height: 100%
        z-index: 1
      .image-block
        width: 100%
        min-height: 270px
        display: flex
        align-items: center
        justify-content: center
        @media (max-width: 767px)
          min-height: 220px
          padding: 0 15px
        .item-image
          display: block
          width: auto
          max-width: 100%
          margin: 0 auto
      .descr-block
        width: 100%
        padding: 15px 28px
        display: flex
        flex-direction: column
        align-items: flex-start
        justify-content: flex-start
        flex: 1 1 auto
        @media (max-width: 767px)
          padding: 15px
        @media (max-width: 575px)
          padding: 15px 20px
        @media (max-width: 420px)
          padding: 10px
        .item-name
          font-family: DINCondensed, sans-serif
          font-weight: 400
          font-size: 24px
          line-height: 104%
          letter-spacing: .03em
          text-transform: uppercase
        .item-descr
          font-weight: 400
          font-size: 14px
          line-height: 143%
          margin: 15px 0 22px
          @media (max-width: 767px)
            margin: 15px 0
          @media (max-width: 420px)
            margin: 10px 0
        .see-link
          display: flex
          align-items: center
          justify-content: flex-start
          color: $main-red-color
          font-weight: 400
          font-size: 18px
          line-height: 139%
          margin: auto 0 22px
          width: 100%
          @media (max-width: 767px)
            margin: auto 0 10px
          .icon
            flex: 0 0 auto
            width: auto
            margin: 0 18px 0 auto
            font-size: 9px
            line-height: 9px
            transition: transform .3s
      &:hover
        position: relative
        z-index: 1
        background: #FFFFFF
        box-shadow: 0px 18px 80px rgba(0, 0, 0, 0.03), 0px 7.51997px 33.4221px rgba(0, 0, 0, 0.0215656), 0px 4.02054px 17.869px rgba(0, 0, 0, 0.0178832), 0px 2.25388px 10.0172px rgba(0, 0, 0, 0.015), 0px 1.19702px 5.32008px rgba(0, 0, 0, 0.0121168), 0px 0.498106px 2.21381px rgba(0, 0, 0, 0.00843437)
        .see-link
          .icon
            transform: translateX(5px)

/* product page */
.product-page
  .main-block__content
    &__left-side
      .descr-block
        max-width: 400px
        @media (max-width: 639px)
          max-width: 100%
    &__right-side
      .product-image
        position: absolute
        left: -100px
        top: -45px
        @media (max-width: 1790px)
          max-width: calc(100% + (100vw - 1170px) / 2)
          left: 0
          top: -70px
        @media (max-width: 1199px)
          max-width: calc(100% - (100vw - 960px) / 2)
          top: -45px
          @media (max-width: 1023px)
            left: 0
          @media (max-width: 959px)
            max-width: 100%
            left: 30px
          @media (max-width: 767px)
            left: 0
            top: 0
          @media (max-width: 639px)
            position: relative
            margin: 0 auto
            width: 414px

.product-info
  margin-top: 50px
  margin-bottom: 20px
  &__options
    margin-top: 30px
    &:nth-of-type(1)
      margin-top: 0
    .options-title
      font-family: DINCondensed, sans-serif
      font-weight: 400
      font-size: 24px
      line-height: 104%
      letter-spacing: .03em
      text-transform: uppercase
    .option-row
      margin-top: 22px
      &:nth-of-type(1)
        margin-top: 0
      &__title
        font-family: DINCondensed, sans-serif
        font-weight: 400
        font-size: 18px
        line-height: 139%
        letter-spacing: .03em
        text-transform: uppercase
      &__items
        display: flex
        align-items: center
        justify-content: flex-start
        flex-wrap: wrap
        margin-top: 5px
        @media (max-width: 575px)
          flex-wrap: nowrap
          max-width: calc(100% + 15px)
          width: calc(100% + 15px)
          overflow-x: scroll
          padding-bottom: 5px
          margin-top: 15px
          @include scrollbars(4px, #D9D9D9, $purple-color, $purple-hover-color, 0)
        .option-item
          flex: 0 0 160px
          max-width: 160px
          margin: 0 9px 9px 0
          @media (max-width: 767px)
            flex: 0 0 140px
            max-width: 140px
            margin: 0 10px 10px 0
          @media (max-width: 639px)
            flex: 0 0 125px
            max-width: 125px
          label
            display: flex
            align-items: center
            justify-content: center
            text-align: center
            position: relative
            input
              position: absolute
              top: 0
              left: 0
              width: 0
              height: 0
              visibility: hidden
              opacity: 0
              &:checked + .label
                background-color: #FDC62C
              &:disabled + .label
                background-color: rgba(#E6E6E6, .3)
                color: rgba(#000, .2)
            .label
              display: flex
              align-items: center
              justify-content: center
              flex-direction: column
              width: 100%
              background-color: rgba(#B4B4B4, .3)
              border-radius: 10px
              font-size: 14px
              line-height: 107%
              padding: 5px
              min-height: 45px
              transition: background-color .3s
              span
                display: block
                margin-top: 2px
    & + .product-info__descr
      border-top: 1px solid rgba(#000, .1)
  &__descr
    margin-top: 40px
    padding-top: 50px
    display: flex
    align-items: flex-start
    justify-content: flex-start
    flex-wrap: wrap
    @media (max-width: 767px)
      padding-top: 0
    &__left-side
      flex: 0 0 calc(60% + 5px)
      max-width: calc(60% + 5px)
      @media (max-width: 767px)
        flex: 0 0 100%
        max-width: 100%
        order: 1
      .descr-title
        font-family: DINCondensed, sans-serif
        font-weight: 400
        font-size: 24px
        line-height: 104%
        letter-spacing: .03em
        text-transform: uppercase
      .descr-list
        margin-top: 25px
        &__item
          margin-top: 10px
          background-color: #fff
          border: 1px solid #F2F2F2
          box-shadow: 0px 27px 80px rgba(0, 0, 0, 0.02), 0px 11.28px 33.4221px rgba(0, 0, 0, 0.0143771), 0px 6.0308px 17.869px rgba(0, 0, 0, 0.0119221), 0px 3.38082px 10.0172px rgba(0, 0, 0, 0.01), 0px 1.79553px 5.32008px rgba(0, 0, 0, 0.00807786), 0px 0.747159px 2.21381px rgba(0, 0, 0, 0.00562291)
          border-radius: 24px
          padding: 0 35px
          @media (max-width: 959px)
            padding: 0 20px
          &.active
            position: relative
            z-index: 1
          .item-title
            font-weight: 700
            font-size: 18px
            line-height: 111%
            position: relative
            cursor: pointer
            padding: 24px 0
            &:after
              content: '\e803'
              font-family: Fontello
              font-style: normal
              font-weight: normal
              speak: never
              display: flex
              align-items: center
              justify-content: center
              margin: 0
              text-decoration: inherit
              text-align: center
              font-variant: normal
              text-transform: none
              color: #fff
              font-size: 16px
              line-height: 16px
              background-color: $purple-color
              transition: background-color .3s
              width: 42px
              height: 42px
              border-radius: 50%
              transform: rotate(90deg)
              padding-left: 2px
              position: absolute
              top: calc(50% - 21px)
              right: -13px
              &:hover
                color: $purple-hover-color
          .item-descr
            font-size: 16px
            line-height: 138%
            letter-spacing: -.02em
            padding: 10px 0 45px
            p
              margin-top: 22px
              &:nth-of-type(1)
                margin-top: 0
          &:nth-of-type(1)
            margin-top: 0
          &.single-item
            border: none
            box-shadow: none
            padding: 0
            border-radius: 0
    &__right-side
      flex: 0 0 calc(40% - 5px)
      max-width: calc(40% - 5px)
      padding-top: 30px
      @media (max-width: 1199px)
        padding-left: 30px
      @media (max-width: 767px)
        flex: 0 0 100%
        max-width: 100%
        padding-left: 0
      .image-block
        display: flex
        align-items: center
        justify-content: center
        @media (max-width: 767px)
          justify-content: flex-start
        .option-preview
          display: block
          width: auto
          max-width: 100%
          margin: 0 auto
          @media (max-width: 767px)
            margin: 0
        & + .descr-block
          margin-top: 55px
      .descr-block
        padding-left: 15%
        display: flex
        align-items: flex-start
        justify-items: flex-start
        flex-wrap: wrap
        margin-top: 30px
        @media (max-width: 959px)
          padding-left: 0
        .block-title
          flex: 0 0 100%
          width: 100%
          font-family: DINCondensed, sans-serif
          font-weight: 400
          font-size: 24px
          line-height: 104%
          letter-spacing: .03em
          text-transform: uppercase
          margin-bottom: 30px
        .block-item
          flex: 0 0 auto
          width: auto
          margin: 0 38px 38px 0
          @media (max-width: 767px)
            margin: 0 15px 15px 0
          @media (max-width: 639px)
            margin: 0 25px 35px 0
          .numb
            font-family: DINCondensed, sans-serif
            font-weight: 400
            font-size: 42px
            line-height: 60%
            letter-spacing: .03em
            text-transform: uppercase
            @media (max-width: 767px)
              font-size: 35px
          p
            font-size: 14px
            line-height: 20px
            letter-spacing: -.02em
            color: $gray-color






