.header
  position: fixed
  top: 0
  left: 0
  width: 100%
  z-index: 100
  transition: background-color .3s
  &__content
    display: flex
    align-items: center
    justify-content: flex-start
    padding: 95px 0 50px
    transition: padding .3s
    @media (max-width: 1200px)
      padding: 40px 0
    @media (max-width: 1024px)
      padding: 30px 0
    @media (max-width: 575px)
      padding: 15px 0
      border-bottom: 1px solid rgba(#fff, .3)
    .logo-link
      display: block
      flex: 0 0 auto
      width: auto
      .logo-image
        display: block
        width: auto
        max-width: 100%
        margin: 0
    .main-menu
      flex: 0 0 auto
      margin: 0 0 0 auto
      @media (max-width: 1023px)
        position: absolute
        top: 100%
        right: 0
        width: 320px
        transform: translateX(100vw)
        height: calc(var(--app-height) - 100%)
        transition: transform .3s
        background-color: $main-red-color
        &.show
          transform: translateX(0)
      @media (max-width: 575px)
        width: 100vw
      &__list
        display: flex
        align-items: center
        justify-content: flex-start
        @media (max-width: 1023px)
          flex-direction: column
          align-items: flex-start
          justify-content: flex-start
          padding: 30px
          border-top: 1px solid rgba(#fff, .3)
        @media (max-width: 575px)
          padding: 30px 15px
      &__item
        flex: 0 0 auto
        width: auto
        margin-left: 30px
        @media (max-width: 1023px)
          margin: 0 0 15px 0
        &:nth-of-type(1)
          margin-left: 0
      &__link
        display: inline-block
        position: relative
        font-family: DINCondensed, sans-serif
        color: #fff
        font-weight: 400
        font-size: 18px
        line-height: 20px
        @media (max-width: 1023px)
          font-size: 24px
          line-height: 30px
        @media (max-width: 575px)
          font-size: 22px
          line-height: 28px
        &:before
          content: ''
          display: block
          width: 0
          height: 1px
          background-color: $yellow-color
          position: absolute
          bottom: -3px
          left: 0
          transition: width .3s
        &:hover
          &:before
            width: 100%
    @import "hamburger"
  & + main
    padding-top: 170px
    background-color: $main-red-color
    border-radius: 0 0 0 175px
    width: 100%
    max-width: 1404px
    margin: 0 auto
    @media (max-width: 1199px)
      border-radius: 0 0 0 115px
      padding-top: 100px
    @media (max-width: 1023px)
      border-radius: 0 0 0 60px
    @media (max-width: 959px)
      padding-top: 85px
    @media (max-width: 419px)
      padding-top: 75px
  &.scroll
    background-color: $main-red-color
    .header__content
      padding: 15px 0

