/* mobile full height fix */
:root {
  --app-height: 100%;
}

* {
  box-sizing: border-box;
  outline: none;
}
*::before, *::after {
  box-sizing: border-box;
}

body,
h1, h2, h3, h4, h5, h6, p,
ul[class], ol[class], li,
figure, figcaption, blockquote,
dl, dd {
  margin: 0;
}

ul[class], ol[class] {
  padding: 0;
  list-style-type: none;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
  color: inherit;
}
a[class] {
  text-decoration: none;
}

input, button, textarea, select {
  font: inherit;
}

button, label {
  cursor: pointer;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  min-width: 320px;
  position: relative;
  font-family: Circe, sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #000;
  background-color: #FFF;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  overflow-x: hidden;
}
body::-webkit-scrollbar {
  width: 6px;
  height: 6px;
  border-radius: 0;
}
body::-webkit-scrollbar-thumb {
  background: #FDC62C;
  border-radius: 0;
  transition: background-color 0.3s;
}
body::-webkit-scrollbar-thumb:hover {
  background: #FFC72C;
}
body::-webkit-scrollbar-track {
  background: #CE0037;
  border-radius: 0;
}
body {
  scrollbar-face-color: #FDC62C;
  scrollbar-track-color: #CE0037;
}
body.overflow {
  overflow: hidden;
}
body.overflow:before {
  content: "";
  display: block;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 10;
}
@media (max-width: 1024px) {
  body.overflow .header {
    background-color: #CE0037;
  }
}

.container {
  width: 100%;
  margin: 0 auto;
  max-width: 1140px;
  padding: 0 15px;
}
@media (max-width: 1199px) {
  .container {
    max-width: 960px;
  }
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100;
  transition: background-color 0.3s;
}
.header__content {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 95px 0 50px;
  transition: padding 0.3s;
  /* settings */
  /* slide animate */
}
@media (max-width: 1200px) {
