.header__content .main-menu__link:before {
  content: "";
  display: block;
  width: 0;
  height: 1px;
  background-color: #FFCA38;
  position: absolute;
  bottom: -3px;
  left: 0;
  transition: width 0.3s;
}
.header__content .main-menu__link:hover:before {
  width: 100%;
}
.header__content .hamburger {
  padding: 0;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition-property: opacity, filter;
  transition-duration: 0.15s;
  transition-timing-function: linear;
  font: inherit;
  color: inherit;
  text-transform: none;
  background-color: transparent;
  border: 0;
  overflow: visible;
  position: relative;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  z-index: 20;
  margin: 0 0 0 auto;
}
@media (max-width: 1023px) {
  .header__content .hamburger {
    display: flex !important;
    padding-top: 2px;
  }
}
.header__content .hamburger.is-active .hamburger-inner {
  background-color: #fff;
}
.header__content .hamburger.is-active .hamburger-inner::before, .header__content .hamburger.is-active .hamburger-inner::after {
  background-color: #fff;
}
.header__content .hamburger-box {
  width: 32px;
  height: 45px;
  display: inline-block;
  position: relative;
}
.header__content .hamburger-inner {
  display: block;
  top: 50%;
  width: 32px;
  height: 2px;
  background-color: #fff;
  border-radius: 4px;
  position: absolute;
  transition-timing-function: ease;
  transition: transform 0.15s;
}
.header__content .hamburger-inner::before, .header__content .hamburger-inner::after {
  width: 32px;
  height: 2px;
  background-color: #fff;
  border-radius: 4px;
  position: absolute;
  transition-property: transform;
  transition-duration: 0.15s;
  transition-timing-function: ease;
}
.header__content .hamburger-inner::before, .header__content .hamburger-inner::after {
  content: "";
  display: block;
}
.header__content .hamburger-inner::before {
  top: -10px;
}
.header__content .hamburger--slider .hamburger-inner {
  top: 12px;
}
.header__content .hamburger--slider .hamburger-inner::before {
  top: 8px;
  right: 0;
  width: 32px;
  transition-property: transform, opacity;
  transition-timing-function: ease;
  transition-duration: 0.15s;
}
.header__content .hamburger--slider .hamburger-inner::after {
  top: 16px;
}
.header__content .hamburger--slider.is-active .hamburger-inner {
  transform: translate3d(4px, 8px, 0) rotate(45deg);
  height: 2px;
  width: 25px;
}
.header__content .hamburger--slider.is-active .hamburger-inner::before {
  transform: rotate(-45deg) translate3d(-5.71429px, -6px, 0);
  opacity: 0;
}
.header__content .hamburger--slider.is-active .hamburger-inner::after {
  transform: translate3d(0, -16px, 0) rotate(-90deg);
  height: 2px;
  width: 25px;
}
