    padding-left: 30px;
  }
}
@media (max-width: 767px) {
  .product-info__descr__right-side {
    flex: 0 0 100%;
    max-width: 100%;
    padding-left: 0;
  }
}
.product-info__descr__right-side .image-block {
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (max-width: 767px) {
  .product-info__descr__right-side .image-block {
    justify-content: flex-start;
  }
}
.product-info__descr__right-side .image-block .option-preview {
  display: block;
  width: auto;
  max-width: 100%;
  margin: 0 auto;
}
@media (max-width: 767px) {
  .product-info__descr__right-side .image-block .option-preview {
    margin: 0;
  }
}
.product-info__descr__right-side .image-block + .descr-block {
  margin-top: 55px;
}
.product-info__descr__right-side .descr-block {
  padding-left: 15%;
  display: flex;
  align-items: flex-start;
  justify-items: flex-start;
  flex-wrap: wrap;
  margin-top: 30px;
}
@media (max-width: 959px) {
  .product-info__descr__right-side .descr-block {
    padding-left: 0;
  }
}
.product-info__descr__right-side .descr-block .block-title {
  flex: 0 0 100%;
  width: 100%;
  font-family: DINCondensed, sans-serif;
  font-weight: 400;
  font-size: 24px;
  line-height: 104%;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  margin-bottom: 30px;
}
.product-info__descr__right-side .descr-block .block-item {
  flex: 0 0 auto;
  width: auto;
  margin: 0 38px 38px 0;
}
@media (max-width: 767px) {
  .product-info__descr__right-side .descr-block .block-item {
    margin: 0 15px 15px 0;
  }
}
@media (max-width: 639px) {
  .product-info__descr__right-side .descr-block .block-item {
    margin: 0 25px 35px 0;
  }
}
.product-info__descr__right-side .descr-block .block-item .numb {
  font-family: DINCondensed, sans-serif;
  font-weight: 400;
  font-size: 42px;
  line-height: 60%;
  letter-spacing: 0.03em;
  text-transform: uppercase;
}
@media (max-width: 767px) {
  .product-info__descr__right-side .descr-block .block-item .numb {
    font-size: 35px;
  }
}
.product-info__descr__right-side .descr-block .block-item p {
  font-size: 14px;
  line-height: 20px;
  letter-spacing: -0.02em;
  color: #BDBDBD;
}